import { gsap } from 'gsap';

function onEnter() {

  const tl = gsap.timeline();

  tl.from('.info h2 span', {
    y: 100,
    duration: 1,
    delay: 0.5,
    ease: 'power3.inOut',
    stagger: 0.3,
    skewY: 5
  });

  gsap.from('.info p', {
    opacity: 0,
    ease: 'power3.inOut',
    duration: 1,
    delay: 2
  });

  gsap.from('.line', {
    strokeDashoffset: 1000,
    duration: 2,
    ease: 'power3.inOut',
    delay: 3
  });
}

export default onEnter;

