import '../styles/playground/dots.scss'
import paper from 'paper'
import { useEffect } from 'react';
import SCRIPT from '../three-js-projects/dots-and-dashes/script.js';

function Dots() {

  useEffect(() => {

    SCRIPT.init();

    document.getElementById('genBtn').addEventListener('click', () => {
      SCRIPT.isAnimate = false;
      SCRIPT.isAutoPlay = false;
      SCRIPT.audio.pause();
      SCRIPT.render();
    });
    
    document.getElementById('animateBtn').addEventListener('click', () => {
      
      document.getElementById('animateBtn').classList.toggle('active'); 
      
      SCRIPT.audio.volume = 0.2;
      
      if(SCRIPT.isAnimate) {
        SCRIPT.isAnimate = false;
        SCRIPT.audio.pause();
      }
      else {
        SCRIPT.audio.play();
        SCRIPT.isAnimate = true;
        SCRIPT.isAutoPlay = false;
        
        SCRIPT.cleanCanvas();
        SCRIPT.count.x = 2;
        SCRIPT.count.y = 1;
        SCRIPT.tick();
      }
    });
    
    return () => {
      SCRIPT.cleanCanvas();
      SCRIPT.isAnimate = false;
      SCRIPT.isAutoPlay = false;
      SCRIPT.audio.pause();
    };

  })

  return (
    <div className="dots-container">
      <ul className="options">
        <li><span id="genBtn">Generate</span></li>
        <li><span id="animateBtn">Animate Creation</span></li>
      </ul>
      <canvas id="paperCanvas" ></canvas>
    </div>
  );
}

export default Dots;
